@media screen and (max-width: 2560px) {
    :root {
        font-size: .9vw;
    }
    /*nav*/
    .Logo{
        height: 3.5rem;
        margin-right: 12rem;
    }
    .NavBar {
        height: 6rem;
        padding: 1rem 7rem;
    }
    .link {
        font-size: 1.5rem;
        padding: .5rem;
    }
    /*home*/
    .modal-content {
        width: 50rem !important;
    }
    .headerbgimg{
        height: 100vh;
    }
    .HeaderText{
        top: 30rem;
    }
    .headerTitle {
        font-size: 4.5rem;
    }
    .headerParagraph {
        width: 32rem;
        font-size: 1rem;
    }
    .Movies button {
        width: 15rem;
        padding: 1rem;
        font-size: 1.5rem;
        margin: .5rem;
    }
    /*popular*/
    .popular {
        height: 45rem;
    }
    /*choose ganres*/
    .section {
        height: 30rem;
    }
    .flip-card {
        width: 13rem;
        margin-right: 1rem;
    }
    .flip-card-back h4 {
        font-size: 1.15rem;
    }
    .flip-card-back small{
        font-size: .8rem;
        line-height: 1px;
    }
    .flip-card-back-data {
        width: 85%;
        margin: .5rem 0;
    }
    /*search*/
    .buttonSearch {
        width: 15rem;
    }
    .modal-body iframe{
        height: 30rem;
    }
    .buttonSearchIMG{
        display: none;
    }
    /*contact*/
    .followIcon img{
        width: 4rem;
    }
    .container{
        max-width: 80% !important;
    }
    .PerContact img{
        width: 3rem;
    }
    .formInput textarea,textarea.form-control{
        height: 8rem !important;
    }
    /*faq*/
    .accordion b {
        font-size: 2rem;
    }
    .accordion p {
        font-size: 1.8rem;
    }
}
@media screen and (max-width: 1024px) {
    :root {
        font-size: 1vw;
    }
    /*home*/
    .headerbgimg{
        height: 100vh;
    }
    .HeaderText{
        top: 40rem;
    }
    form .input {
        width: 30rem;
    }
    /*search*/
    .modal-content {
        width: 45rem !important;
    }
}
@media screen and (max-width: 768px) {
    .headerbgimg{
        height: 90vh;
    }
    .HeaderText{
        top: 50rem;
    }
}
@media screen and (max-width: 426px) {
    :root {
        font-size: 1.5vw;
    }
    /*navBar*/
    .NavBar {
        height: 10rem;
        padding: 1rem 4rem;
    }
    .Logo {
        height: 4.5rem;
        margin-right: 5rem;
    }
    .link {
        font-size: 2rem;
        padding: 1rem;
    }
    /*home*/
    .headerTitle {
        font-size: 5.5rem;
    }
    h2{
        font-size: 6rem !important;
    }
    .headerbgimg {
        width: 100%;
        object-fit: cover;
        height: 100vh;
    }
    .HeaderText{
        top: 73rem;
    }
    .headerTitle {
        font-size: 5.5rem;
    }
    .headerParagraph {
        width: 40rem;
        font-size: 1.5rem;
    }
    .HeaderTextRight {
        margin-top: 10rem;
    }
    .HeaderTextRight h4{
        font-size: 2rem;
    }
    .Movies button {
        width: 20rem;
        padding: 2rem;
        font-size: 2rem;
        margin: 1rem;
    }
    /*choose genres*/
    .section {
        height: 40rem;
    }
    .flip-card {
        width: 18rem;
        margin-right: 2rem;
    }
    .flip-card-back h4 {
        font-size: 2rem;
    }
    .flip-card-back small {
        font-size: 1.3rem;
         line-height: normal;
    }
    .flip-card-back-data {
        width: 90%;
        margin: 1rem 0;
    }
    /*popular*/
    .swiper-pagination {
        display: none;
    }
    .modal-content {
        left: -40%;
    }
    .linkContact{
        margin-left: 2rem;
    }
    .popular {
        height: 55rem;
    }
    /*search*/
    form .input {
        width: 28rem;
    }
    .buttonSearch {
        display: none;
    }
    .buttonSearchIMG{
        display: block;
        background: none;
        position: absolute;
        right: 1rem;
        top: .7rem;
        border: none;
        outline: none;
    }
    .buttonSearchIMG img{
        width: 1rem;
    }
    /*contact*/
    .formInput textarea, textarea.form-control{
        height: 9rem !important;
    }
    /*faq*/
    .accordion b {
        font-size: 2.5rem;
    }
    .accordion p {
        font-size: 2rem;
    }
}
@media screen and (max-width: 376px) {
    :root {
        font-size: 1.5vw;
    }
    /*home*/
    .headerbgimg{
        height: 60vh;
    }
    .HeaderText{
        top: 45rem;
        font-size: 2rem;
    }
    /*search*/
    form .input {
        width: 20rem;
    }
    .buttonSearch {
        width: 10rem;
    }
    .modal-content {
        left: -45%;
    }
}
@media screen and (max-width: 376px) {
    /*home*/
    .headerbgimg{
        height: 55vh;
    }
    .HeaderText{
        top: 50rem;
    }
}